
<template>
  <div>
    <div ref="storeEcharts" :style="{ width: '1075px', height: '380px' }"></div>
  </div>
</template>

<script>
import { http } from "@/utils/request";
import { getIntToRoman } from "./util";
import * as echarts from "echarts";
export default {
  props: ["newOption"],
  data() {
    return {
      datas: [],
      list: [],
      lists: [],
      year: [],
      chart: null,
      labelOption: {},
      option: {
        tooltip: {
          trigger: "axis",
          formatter: (params) => {
            const arr = params.map((it) => {
              return `${it.marker}${it.seriesName}：${getIntToRoman(
                it.value
              )}<br>`;
            });
            return arr.join(" ");
          },
        },
        legend: {
          data: ["水质目标", "本月水质", "本年累积平均水质"],
          textStyle: {
            fontWeight: "normal",
            fontSize: "20",
            color: "#fff",
            textAlign: "center",
          },
        },
        xAxis: [
          {
            type: "category",
            axisTick: { show: false },
            data: [
              "凤河-凤河营闸(国考)",
              "龙河-三小营(国考)",
              "永定河-永定河桥(市考)",
              "新凤河-烧饼庄闸(市考)",
              "永兴河-荆家务村南(市考)",
              "小龙河-圆梦家园北侧拱桥(市考)",
              "凉水河-马驹桥(国考)",
            ],
            axisLabel: {
              rotate: 13,
              textStyle: {
                color: "#fff", //更改坐标轴文字颜色
                fontSize: 14, //更改坐标轴文字大小
              },
            },
          },
        ],
        yAxis: {
          min: 0,
          max: 5,
          color: "#fff",
          axisLabel: {
            formatter: function (value) {
              var texts = [];
              if (value == 0) {
                texts.push("无水");
              } else if (value <= 1) {
                texts.push("Ⅰ");
              } else if (value <= 2) {
                texts.push("Ⅱ");
              } else if (value <= 3) {
                texts.push("Ⅲ");
              } else if (value <= 4) {
                texts.push("Ⅳ");
              } else {
                texts.push("Ⅴ");
              }
              return texts;
            },
            color: "#fff",
            fontSize: 18,
          },
        },
        series: [
          {
            name: "水质目标",
            type: "bar",
            barGap: 0,
            label: this.labelOption,
            emphasis: {
              focus: "series",
            },
            data: [],
            itemStyle: {
              color: "#5f84ef",
            },
          },
          {
            name: "本月水质",
            type: "bar",
            label: this.labelOption,
            emphasis: {
              focus: "series",
            },
            data: [],
            itemStyle: {
              color: "#87d69f",
            },
          },
          {
            name: "本年累积平均水质",
            type: "bar",
            label: this.labelOption,
            emphasis: {
              focus: "series",
            },
            data: [],
            itemStyle: {
              color: "#e7b252",
            },
          },
        ],
      },
    };
  },
  watch: {
    newOption(val) {
      // console.log(val);
      this.dataArr = val;
      this.initChart();
    },
  },
  mounted() {
    this.dataArr = this.newOption;
    // this.getEchartslist();
    this.initChart();
  },
  created() {
    this.getEchartslist();
  },

  methods: {
    getEchartslist() {
      let json = {
        username: "api",
        password: "a123456b",
        section_type: "1,2",

        // {"username": "api", "password": "a123456b", "section_type": "1,2"}
      };
      http
        .get(
          "https://api-dx-smart-city.airqualitychina.cn:9998/v1/dashboard/water/section-data",
          {
            params: {
              json,
            },
          }
        )
        .then((res) => {
          res.data.result.map((item, index) => {
            this.list.push(item.wq_level_value);
            this.lists.push(item.wq_object_value);
            this.year.push(item.wq_year_level_value);
          });
          this.option.series[0].data = this.list;
          this.option.series[1].data = this.lists;
          this.option.series[2].data = this.year;
          // console.log(this.option.series[0].data)
          this.chart.setOption(this.option, true);
        });
    },

    initChart() {
      // 基于准备好的dom，初始化echarts实例
      this.chart = echarts.init(this.$refs.storeEcharts);
      // this.chart.setOption(this.myOption);
      const posList = [
        "left",
        "right",
        "top",
        "bottom",
        "inside",
        "insideTop",
        "insideLeft",
        "insideRight",
        "insideBottom",
        "insideTopLeft",
        "insideTopRight",
        "insideBottomLeft",
        "insideBottomRight",
      ];
      app.configParameters = {
        rotate: {
          min: -90,
          max: 90,
        },
        align: {
          options: {
            left: "left",
            center: "center",
            right: "right",
          },
        },
        verticalAlign: {
          options: {
            top: "top",
            middle: "middle",
            bottom: "bottom",
          },
        },
        position: {
          options: posList.reduce(function (map, pos) {
            map[pos] = pos;
            return map;
          }, {}),
        },
        distance: {
          min: 0,
          max: 100,
        },
      };
      app.config = {
        rotate: 90,
        align: "left",
        verticalAlign: "middle",
        position: "insideBottom",
        distance: 15,
        onChange: function () {
          const labelOption = {
            rotate: app.config.rotate,
            align: app.config.align,
            verticalAlign: app.config.verticalAlign,
            position: app.config.position,
            distance: app.config.distance,
          };
          myChart.setOption({
            series: [
              {
                label: labelOption,
              },
              {
                label: labelOption,
              },
              {
                label: labelOption,
              },
              {
                label: labelOption,
              },
            ],
          });
        },
      };
      this.labelOption = {
        show: true,
        position: app.config.position,
        distance: app.config.distance,
        align: app.config.align,
        verticalAlign: app.config.verticalAlign,
        rotate: app.config.rotate,
        formatter: "{c}  {name|{a}}",
        fontSize: 16,
        rich: {
          name: {},
        },
      };
    },
  },
};
</script>
