export const getIntToRoman = (num) => {
    const value = [
        [1000, "M"],
        [900, "CM"],
        [500, "D"],
        [400, "CD"],
        [100, "C"],
        [90, "XC"],
        [50, "L"],
        [40, "XL"],
        [10, "X"],
        [9, "IX"],
        [5, "V"],
        [4, "IV"],
        [1, "I"],
    ];
    const roman = [];
    let nums = Number(num);
    for (const [val, symbol] of value) {
        while (nums >= val) {
            // 找到不超过 nums 的最大符号值
            nums -= val; // 找到 nums 的最大符号值减去
            roman.push(symbol); // 最大符号放在 roman 中
        }
        if (nums === 0) {
            // 直到 nums 被减到 0
            break;
        }
    }
    return roman.join("");
}