<template>
  <div ref="id" style="width: 100%;height:100%;">
    <!-- 为 ECharts 准备一个具备大小（宽高）的 DOM -->
  </div>
</template>

<script>
import * as echarts from "echarts";
export default {
  props: {
    options: {
      type: Object,
      default: {},
    },
    // title
  },
  data() {
    return {
      myChart: null,
      myOptions: {},
    };
  },
  created() {
    // this.initCharts();
  },
  mounted() {
    // 初始化图表
    this.initCharts();
    console.log(this.options, "+++++1111");
  },
  updated() {
    // 初始化图表
    this.initCharts();
  },

  methods: {
    // 初始化图表
    initCharts() {
      console.log(this.$refs.id.style);
      this.myChart = echarts.init(this.$refs.id);

      this.myChart.setOption(this.options, false);
      window.addEventListener("resize", this.onResize);
    },
    onResize() {
      this.myChart.resize();
    },
  },
};
</script>

